import { Component, ChangeDetectionStrategy } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

@Component({
	selector: 'bp-career-page',
	templateUrl: './career-page.component.html',
	styleUrls: ['./career-page.component.scss'],
	changeDetection: ChangeDetectionStrategy.OnPush
})
export class CareerPageComponent {
	background = 'regular';

	career$: Observable<ICareer>;

	private careers: ICareer[] = require('./careers.json');

	constructor(private route: ActivatedRoute) {
		this.career$ = this.route.params.pipe(
			map(({ careerId }) => this.careers.find(it => it.id === +careerId))
		);
	}
}

interface ICareer {
	id: number;
	name: string;
	location: string;
	description: string;
	overview: {
		text: string;
		items: string[];
	};
	requirements: {
		text: string;
		items: string[];
	};
}
